<template>
  <div>
    <el-carousel :interval="4000" type="card" height="200px">
      <el-carousel-item v-for="item in images" :key="item">
        <el-image
          style="height: 280px"
          :src="item"
          fit="scale-down"></el-image>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
  import home1 from '../assets/index_images/1.jpg'
  import home2 from '../assets/index_images/2.jpg'
  import home3 from '../assets/index_images/3.jpg'
  import home4 from '../assets/index_images/4.jpg'
  import home5 from '../assets/index_images/5.jpg'
  import home6 from '../assets/index_images/6.jpg'
  import home7 from '../assets/index_images/7.jpg'
  import home8 from '../assets/index_images/8.jpg'

  export default {
    name: "Index",
    data() {
      return {
        images: [home1, home2, home3, home4, home5, home6, home7, home8]
      }
    }
  }
</script>

<style scoped>

</style>



